#usuario{
  border-radius: 10px;
  background-color: white;
  width: 700px;
  height: 20px;
  margin: auto;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container {
  margin: 30px auto;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  
}






















